import React from 'react';
import { Switch } from 'antd';
import classnames from 'classnames';
import { shallow } from 'zustand/shallow';
import { useStore } from '@/module/tianditu/store';
import styles from './index.less';

const selector = (store: any) => ({
  mapOptions: store.mapOptions,
  updateMapOptions: store.updateMapOptions,
});

// 参数设置
const BasePanel = () => {
  const { mapOptions, updateMapOptions } = useStore(selector, shallow);

  const handldSwitch = (key: string, checked: boolean) => {
    const options: any = {};
    options[key] = checked;
    updateMapOptions(options);
  };

  return (
    <div className={styles.list}>
      <div className={classnames(styles.listItem, styles.rowDirection)}>
        <div className={styles.title}>是否启用地图拖拽</div>
        <Switch
          className={styles.action}
          defaultChecked={mapOptions?.isEnableDrag}
          onChange={(checked) => handldSwitch('isEnableDrag', checked)}
        />
      </div>
      <div className={classnames(styles.listItem, styles.rowDirection)}>
        <div className={styles.title}>是否启用滚轮放大缩小</div>
        <Switch
          className={styles.action}
          defaultChecked={mapOptions?.isScrollWheelZoom}
          onChange={(checked) => handldSwitch('isScrollWheelZoom', checked)}
        />
      </div>
      <div className={classnames(styles.listItem, styles.rowDirection)}>
        <div className={styles.title}>是否启用双击放大</div>
        <Switch
          className={styles.action}
          defaultChecked={mapOptions?.isDoubleClickZoom}
          onChange={(checked) => handldSwitch('isDoubleClickZoom', checked)}
        />
      </div>
      <div className={classnames(styles.listItem, styles.rowDirection)}>
        <div className={styles.title}>是否启用键盘操作</div>
        <Switch
          className={styles.action}
          defaultChecked={mapOptions?.isKeyboard}
          onChange={(checked) => handldSwitch('isKeyboard', checked)}
        />
      </div>
      <div className={classnames(styles.listItem, styles.rowDirection)}>
        <div className={styles.title}>是否启用惯性拖拽</div>
        <Switch
          className={styles.action}
          defaultChecked={mapOptions?.isInertia}
          onChange={(checked) => handldSwitch('isInertia', checked)}
        />
      </div>
      <div className={classnames(styles.listItem, styles.rowDirection)}>
        <div className={styles.title}>是否启用连续缩放</div>
        <Switch
          className={styles.action}
          defaultChecked={mapOptions?.isContinuousZoom}
          onChange={(checked) => handldSwitch('isContinuousZoom', checked)}
        />
      </div>
      <div className={classnames(styles.listItem, styles.rowDirection)}>
        <div className={styles.title}>是否启用双指缩放</div>
        <Switch
          className={styles.action}
          defaultChecked={mapOptions?.isPinchToZoom}
          onChange={(checked) => handldSwitch('isPinchToZoom', checked)}
        />
      </div>
      <div className={classnames(styles.listItem, styles.rowDirection)}>
        <div className={styles.title}>是否自动适应容器尺寸</div>
        <Switch
          className={styles.action}
          defaultChecked={mapOptions?.isAutoResize}
          onChange={(checked) => handldSwitch('isAutoResize', checked)}
        />
      </div>
    </div>
  );
};

export default BasePanel;
