import React from 'react';
import { Switch, Segmented, Space, Avatar } from 'antd';
import classnames from 'classnames';
import { shallow } from 'zustand/shallow';
import { useStore } from '@/module/tianditu/store';
import styles from './index.less';

const selector = (store: any) => ({
  controlOptions: store.controlOptions,
  updateControlOptions: store.updateControlOptions,
  setMapType: store.setMapType,
});

// 控件设置
const ControlPanel = () => {
  const { controlOptions, updateControlOptions, setMapType } = useStore(selector, shallow);

  const handldSwitch = (key: string, checked: boolean) => {
    const options: any = {};
    options[key] = checked;
    updateControlOptions(options);
  };

  const handleSetMapType = (type: string) => {
    setMapType(type);
  };

  return (
    <div className={styles.list}>
      <div className={classnames(styles.listItem, styles.rowDirection)}>
        <div className={styles.title}>是否展示版权控件</div>
        <Switch
          className={styles.action}
          defaultChecked={controlOptions?.isShowCopyright}
          onChange={(checked) => handldSwitch('isShowCopyright', checked)}
        />
      </div>
      <div className={classnames(styles.listItem, styles.rowDirection)}>
        <div className={styles.title}>是否显示缩放控件</div>
        <Switch
          className={styles.action}
          defaultChecked={controlOptions?.isShowZoomControl}
          onChange={(checked) => handldSwitch('isShowZoomControl', checked)}
        />
      </div>
      <div className={classnames(styles.listItem, styles.rowDirection)}>
        <div className={styles.title}>是否显示比例尺</div>
        <Switch
          className={styles.action}
          defaultChecked={controlOptions?.isShowScaleControl}
          onChange={(checked) => handldSwitch('isShowScaleControl', checked)}
        />
      </div>
      <div className={classnames(styles.listItem, styles.rowDirection)}>
        <div className={styles.title}>是否显示鹰眼图</div>
        <Switch
          className={styles.action}
          defaultChecked={controlOptions?.isShowOverviewMap}
          onChange={(checked) => handldSwitch('isShowOverviewMap', checked)}
        />
      </div>
      <div className={classnames(styles.listItem, styles.rowDirection)}>
        <div className={styles.title}>是否显示地图类型</div>
        <Switch
          className={styles.action}
          defaultChecked={controlOptions?.isShowMapTypeControl}
          onChange={(checked) => handldSwitch('isShowMapTypeControl', checked)}
        />
      </div>
      <div className={classnames(styles.listItem, styles.colDirection)}>
        <Space direction='vertical'>
          <div className={styles.title}>手动切换地图类型</div>
          <Segmented
            block
            defaultValue={controlOptions?.mapType}
            options={[
              {
                label: (
                  <div style={{ margin: '0 -11px', padding: '4px 0 2px 0' }}>
                    <Avatar size={44} style={{ borderRadius: '2px' }} shape="square" src="https://api.tianditu.gov.cn/v4.0/image/map/maptype/vector.png" />
                    <div style={{ fontSize: 10, lineHeight: '20px' }}>地图</div>
                  </div>
                ),
                value: 'TMAP_NORMAL_MAP',
              },
              {
                label: (
                  <div style={{ margin: '0 -11px', padding: '4px 0 2px 0' }}>
                    <Avatar size={44} style={{ borderRadius: '2px' }} shape="square" src="https://api.tianditu.gov.cn/v4.0/image/map/maptype/satellite.png" />
                    <div style={{ fontSize: 10, lineHeight: '20px' }}>卫星</div>
                  </div>
                ),
                value: 'TMAP_SATELLITE_MAP',
              },
              {
                label: (
                  <div style={{ margin: '0 -11px', padding: '4px 0 2px 0' }}>
                    <Avatar size={44} style={{ borderRadius: '2px' }} shape="square" src="https://api.tianditu.gov.cn/v4.0/image/map/maptype/satellitepoi.png" />
                    <div style={{ fontSize: 10, lineHeight: '20px' }}>卫星混合</div>
                  </div>
                ),
                value: 'TMAP_HYBRID_MAP',
              },
              {
                label: (
                  <div style={{ margin: '0 -11px', padding: '4px 0 2px 0' }}>
                    <Avatar size={44} style={{ borderRadius: '2px' }} shape="square" src="https://api.tianditu.gov.cn/v4.0/image/map/maptype/terrain.png" />
                    <div style={{ fontSize: 10, lineHeight: '20px' }}>地形</div>
                  </div>
                ),
                value: 'TMAP_TERRAIN_MAP',
              },
              {
                label: (
                  <div style={{ margin: '0 -11px', padding: '4px 0 2px 0' }}>
                    <Avatar size={44} style={{ borderRadius: '2px' }} shape="square" src="https://api.tianditu.gov.cn/v4.0/image/map/maptype/terrainpoi.png" />
                    <div style={{ fontSize: 10, lineHeight: '20px' }}>地形混合</div>
                  </div>
                ),
                value: 'TMAP_TERRAIN_HYBRID_MAP',
              }
            ]}
            onChange={(type) => handleSetMapType(`${type}`)}
          />
        </Space>
      </div>
    </div>
  );
};

export default ControlPanel;
