import React from 'react';
import classnames from 'classnames';
import { Col, Row, Slider, ColorPicker } from 'antd';
import { shallow } from 'zustand/shallow';
import { useStore } from '@/module/tianditu/store';
import styles from './index.less';

const themes = [
  {
    label: '标准',
    value: 'normal',
    setting: {
      container: { BColor: '#fff' },
      base: { opacity: 1, grayscale: 0, invert: 0, hueRotate: '0deg' },
      cover: { opacity: 1, grayscale: 0, invert: 0, hueRotate: '0deg' },
    }
  },
  {
    label: '月光银',
    value: 'light',
    setting: {
      container: { BColor: '#f2f2f2' },
      base: { opacity: 0.5, grayscale: 1, invert: 0, hueRotate: '0deg' },
      cover: { opacity: 1, grayscale: 0, invert: 0, hueRotate: '0deg' },
    }
  },
  {
    label: '远山黛',
    value: 'whitesmoke',
    setting: {
      container: { BColor: '#ccd6d7' },
      base: { opacity: 0.5, grayscale: 0.9, invert: 0, hueRotate: '180deg' },
      cover: { opacity: 1, grayscale: 0, invert: 0, hueRotate: '0deg' },
    }
  },
  {
    label: '草色青',
    value: 'fresh',
    setting: {
      container: { BColor: '#f7f6f0' },
      base: { opacity: 0.5, grayscale: 0, invert: 0, hueRotate: '45deg' },
      cover: { opacity: 1, grayscale: 0, invert: 0, hueRotate: '0deg' },
    }
  },
  {
    label: '雅士灰',
    value: 'grey',
    setting: {
      container: { BColor: '#070e17' },
      base: { opacity: 0.5, grayscale: 1, invert: 1, hueRotate: '0deg' },
      cover: { opacity: 1, grayscale: 1, invert: 1, hueRotate: '0deg' },
    }
  },
  {
    label: '马卡龙',
    value: 'macaron',
    setting: {
      container: { BColor: '#f3f8ff' },
      base: { opacity: 0.5, grayscale: 0, invert: 0, hueRotate: '90deg' },
      cover: { opacity: 1, grayscale: 0, invert: 0, hueRotate: '0deg' },
    }
  },
  {
    label: '靛青蓝',
    value: 'blue',
    setting: {
      container: { BColor: '#1b5f62' },
      base: { opacity: 0.5, grayscale: 1, invert: 1, hueRotate: '0deg' },
      cover: { opacity: 0.5, grayscale: 1, invert: 1, hueRotate: '0deg' },
    }
  },
  {
    label: '极夜蓝',
    value: 'darkblue',
    setting: {
      container: { BColor: '#466399' },
      base: { opacity: 0.5, grayscale: 1, invert: 1, hueRotate: '0deg' },
      cover: { opacity: 0.5, grayscale: 1, invert: 1, hueRotate: '0deg' }
    }
  },
];

const selector = (store: any) => ({
  themeOptions: store.themeOptions,
  updateThemeOptions: store.updateThemeOptions,
});

// 风格设置
const ColorPanel = () => {
  const { themeOptions, updateThemeOptions } = useStore(selector, shallow);
  const handleUpdateCustom = (key: string, action: string, value: any) => {
    const customTheme: any = {
      ...themeOptions,
      label: '自定义',
      value: 'custom',
    };
    if (action === 'hueRotate') {
      customTheme.setting[key][action] = value + 'deg';
    } else if (['opacity', 'grayscale', 'invert'].includes(action)) {
      customTheme.setting[key][action] = +(value / 100).toFixed(2);
    } else {
      customTheme.setting[key][action] = value;
    }
    updateThemeOptions(customTheme);
  };
  return (
    <Row gutter={16} className={styles.themeCards}>
      {themes.map((item, index) => (
        <Col span={12} key={item.value}>
          <div
            className={classnames(styles.themeCard, styles.pointer)}
            style={{ backgroundColor: item?.setting?.container?.BColor || '#fff' }}
            onClick={() => updateThemeOptions(item || {})}
          >
            <span className={styles.mapText}>{item.label}</span>
            <div
              className={styles.mapCover}
              style={{
                opacity: item?.setting?.cover?.opacity || 1,
                filter: [
                  `grayscale(${item?.setting?.cover?.grayscale || 0})`,
                  `invert(${item?.setting?.cover?.invert || 0})`,
                  `hue-rotate(${item?.setting?.cover?.hueRotate || '0deg'})`,
                ].join(' ')
              }}
            />
            <div
              className={styles.mapBase}
              style={{
                opacity: item?.setting?.base?.opacity || 1,
                filter: [
                  `grayscale(${item?.setting?.base?.grayscale || 0})`,
                  `invert(${item?.setting?.base?.invert || 0})`,
                  `hue-rotate(${item?.setting?.base?.hueRotate || '0deg'})`,
                ].join(' ')
              }}
            />
          </div>
        </Col>
      ))}
      <Col span={24}>
        <div
          className={styles.themeCard}
          style={{ backgroundColor: themeOptions?.setting?.container?.BColor || '#fff' }}
        >
          <div className={styles.mapPanel}>
            <span className={styles.mapText}>自定义</span>
            <div
              className={styles.mapCover}
              style={{
                opacity: themeOptions?.setting?.cover?.opacity || 1,
                filter: [
                  `grayscale(${themeOptions?.setting?.cover?.grayscale || 0})`,
                  `invert(${themeOptions?.setting?.cover?.invert || 0})`,
                  `hue-rotate(${themeOptions?.setting?.cover?.hueRotate || '0deg'})`,
                ].join(' ')
              }}
            />
            <div
              className={styles.mapBase}
              style={{
                opacity: themeOptions?.setting?.base?.opacity || 1,
                filter: [
                  `grayscale(${themeOptions?.setting?.base?.grayscale || 0})`,
                  `invert(${themeOptions?.setting?.base?.invert || 0})`,
                  `hue-rotate(${themeOptions?.setting?.base?.hueRotate || '0deg'})`,
                ].join(' ')
              }}
            />
          </div>
          <div className={styles.mapSetting}>
            {[
              { title: '底图-透明', key: 'base', action: 'opacity' },
              { title: '底图-灰度', key: 'base', action: 'grayscale' },
              { title: '底图-反色', key: 'base', action: 'invert' },
              { title: '底图-色相', key: 'base', action: 'hueRotate' },
              { title: '遮罩-透明', key: 'cover', action: 'opacity' },
              { title: '遮罩-灰度', key: 'cover', action: 'grayscale' },
              { title: '遮罩-反色', key: 'cover', action: 'invert' },
              { title: '遮罩-色相', key: 'cover', action: 'hueRotate' },
              { title: '地图-颜色', key: 'container', action: 'BColor' },
            ].map((i, index) => {
              const data = themeOptions.setting[i.key][i.action];
              let value: any = 0;
              if (i.action === 'hueRotate') {
                value = data.replace('deg', '');
              } else if (['opacity', 'grayscale', 'invert'].includes(i.action)) {
                value = Math.round(+((data + '').replace('%', '')) * 100);
              } else {
                value = data;
              }
              return (
                <Row className={styles.mapSettingRow} key={index}>
                  <Col span={6}>{i.title}</Col>
                  {i.action !== 'BColor' ? (
                    <>
                      <Col span={13} className={styles.mapSettingSlider}>
                        <Slider
                          min={1}
                          max={i.action === 'hueRotate' ? 360 : 100}
                          onChange={value => handleUpdateCustom(i.key, i.action, value)}
                          value={value}
                        />
                      </Col>
                      <Col span={5}>{`${value}${i.action === 'hueRotate' ? 'deg' : '%'}`}</Col>
                    </>
                  ) : (
                    <Col span={18} className={styles.mapSettingColor}>
                      <ColorPicker
                        showText
                        getPopupContainer={triggerNode => triggerNode.parentNode as any}
                        value={value}
                        placement="topRight"
                        onChange={(color, value) => handleUpdateCustom(i.key, i.action, value)}
                      />
                    </Col>
                  )}
                </Row>
              );
            })}
          </div>
        </div>
      </Col>
    </Row>
  );
};

export default ColorPanel;
