import React, { useState } from 'react';
import { Button, Drawer } from 'antd';
import {
  CaretRightOutlined,
  CaretLeftOutlined,
  SettingFilled,
  CloseCircleFilled,
} from '@ant-design/icons';
import classnames from 'classnames';
import { isInMicroApp } from '@/utils/microApp';
import List from './list';
import Setting from './setting';
import styles from './index.less';

function App() {
  const [mainDrawer, setMainDrawer] = useState(true);
  const [settingDrawer, setSettingDrawer] = useState(false);

  const switchMainDrawer = () => {
    if (mainDrawer) {
      setSettingDrawer(false);
    }
    setMainDrawer(!mainDrawer);
  };

  const switchSettingDrawer = () => {
    setSettingDrawer(!settingDrawer);
  };

  return (
    <Drawer
      width={360}
      closable={false}
      open={mainDrawer}
      mask={false}
      getContainer={false}
      rootClassName={styles.wrap}
      className={styles.mainContainer}
      title={
        <div className={classnames(styles.drawerHeader, isInMicroApp && styles.micro)}>
          <span>玄奘西行</span>
          <SettingFilled onClick={switchSettingDrawer} />
        </div>
      }
      push={{ distance: 300 }}
    >
      <Button
        type="primary"
        className={styles.drawerWrapBtn}
        onClick={switchMainDrawer}
        icon={mainDrawer ? <CaretRightOutlined /> : <CaretLeftOutlined />}
      />
      <List />
      <Drawer
        width={300}
        closable={false}
        open={settingDrawer}
        mask={false}
        className={styles.settingContainer}
        rootClassName={styles.settingWrap}
        getContainer={false}
        title={
          <div className={classnames(styles.drawerHeader, isInMicroApp && styles.micro)}>
            <span>地图配置</span>
            <CloseCircleFilled onClick={switchSettingDrawer} />
          </div>
        }
      >
        <Setting />
      </Drawer>
    </Drawer>
  );
}

export default App;
